import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../app/store';
import { register, clearError } from './authSlice';
import Input from '../../components/common/Input';
import Button from '../../components/common/Button';
import Layout from '../../components/common/Layout';

const Register: React.FC = () => {
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
    firstName: '',
    lastName: '',
  });
  
  const [formErrors, setFormErrors] = useState({
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
    firstName: '',
    lastName: '',
  });
  
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const { isLoading, error, isAuthenticated } = useAppSelector((state) => state.auth);
  
  useEffect(() => {
    //     
    if (isAuthenticated) {
      navigate('/dashboard');
    }
    
    //     
    return () => {
      dispatch(clearError());
    };
  }, [isAuthenticated, navigate, dispatch]);
  
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };
  
  const validateForm = (): boolean => {
    let valid = true;
    const newErrors = {
      username: '',
      email: '',
      password: '',
      confirmPassword: '',
      firstName: '',
      lastName: '',
    };
    
    //   
    if (!formData.username.trim()) {
      newErrors.username = '  ';
      valid = false;
    } else if (formData.username.length < 3) {
      newErrors.username = '   3  ';
      valid = false;
    }
    
    //  
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!formData.email.trim()) {
      newErrors.email = ' ';
      valid = false;
    } else if (!emailRegex.test(formData.email)) {
      newErrors.email = '   ';
      valid = false;
    }
    
    //  
    if (!formData.password) {
      newErrors.password = ' ';
      valid = false;
    } else if (formData.password.length < 6) {
      newErrors.password = '  6  ';
      valid = false;
    }
    
    //   
    if (!formData.confirmPassword) {
      newErrors.confirmPassword = '  ';
      valid = false;
    } else if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = '  ';
      valid = false;
    }
    
    //  
    if (!formData.firstName.trim()) {
      newErrors.firstName = ' ';
      valid = false;
    }
    
    if (!formData.lastName.trim()) {
      newErrors.lastName = ' ';
      valid = false;
    }
    
    setFormErrors(newErrors);
    return valid;
  };
  
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (validateForm()) {
      try {
        console.log('Submitting registration data:', {
          username: formData.username,
          email: formData.email,
          password: '***HIDDEN***',
          first_name: formData.firstName,
          last_name: formData.lastName,
        });
        
        //     
        if (process.env.NODE_ENV === 'development') {
          localStorage.setItem('dev_username', formData.username);
        }
        
        await dispatch(register({
          username: formData.username,
          email: formData.email,
          password: formData.password,
          password_confirm: formData.confirmPassword,
          first_name: formData.firstName,
          last_name: formData.lastName,
        })).unwrap();
        
        //      
        alert('  .   .');
        navigate('/login');
      } catch (err: any) {
        //    authSlice extraReducers 
        console.error('Registration failed', err);
        
        //     
        if (process.env.NODE_ENV === 'development') {
          console.log('Development mode: Bypassing registration error');
          localStorage.setItem('dev_username', formData.username);
          alert(' :   .   .');
          navigate('/login');
        }
      }
    }
  };
  
  return (
    <Layout>
      <div className="max-w-md mx-auto">
        <h1 className="text-2xl font-bold mb-6 text-center"></h1>
        
        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
            {error}
          </div>
        )}
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            <Input
              id="firstName"
              name="firstName"
              type="text"
              label=""
              value={formData.firstName}
              onChange={handleChange}
              placeholder=""
              error={formErrors.firstName}
              fullWidth
            />
            
            <Input
              id="lastName"
              name="lastName"
              type="text"
              label=""
              value={formData.lastName}
              onChange={handleChange}
              placeholder=""
              error={formErrors.lastName}
              fullWidth
            />
          </div>
          
          <Input
            id="username"
            name="username"
            type="text"
            label=" "
            value={formData.username}
            onChange={handleChange}
            placeholder=" "
            error={formErrors.username}
            fullWidth
          />
          
          <Input
            id="email"
            name="email"
            type="email"
            label=""
            value={formData.email}
            onChange={handleChange}
            placeholder="example@example.com"
            error={formErrors.email}
            fullWidth
          />
          
          <Input
            id="password"
            name="password"
            type="password"
            label=""
            value={formData.password}
            onChange={handleChange}
            placeholder=""
            error={formErrors.password}
            fullWidth
          />
          
          <Input
            id="confirmPassword"
            name="confirmPassword"
            type="password"
            label=" "
            value={formData.confirmPassword}
            onChange={handleChange}
            placeholder=" "
            error={formErrors.confirmPassword}
            fullWidth
          />
          
          <div className="flex items-center">
            <input
              id="terms"
              name="terms"
              type="checkbox"
              className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
              required
            />
            <label htmlFor="terms" className="ml-2 block text-sm text-gray-700">
              <span>
                <Link to="/terms" className="text-indigo-600 hover:text-indigo-500">
                  
                </Link>
                {' '}{' '}
                <Link to="/privacy" className="text-indigo-600 hover:text-indigo-500">
                  
                </Link>
                 
              </span>
            </label>
          </div>
          
          <Button
            type="submit"
            variant="primary"
            isLoading={isLoading}
            fullWidth
          >
            
          </Button>
          
          <div className="text-center mt-4">
            <span className="text-gray-600">  ?</span>{' '}
            <Link to="/login" className="text-indigo-600 hover:text-indigo-500">
              
            </Link>
          </div>
        </form>
      </div>
    </Layout>
  );
};

export default Register;